.searchBar {
  text-align: center;
  margin: auto;
  padding: 50px;
  font-size: 2rem;
  margin: 70px 0px;
}
.container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 1.5rem;
  gap: 1.5rem;
  margin: 2rem 3vw;
  padding: 0;
  list-style-type: none;
}
.photoViewer {
  position: relative;
  overflow: hidden;
  border: 5px solid #000000;
  border-radius: 1.25em;
}
img {
  object-fit: cover;
}

.card_overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 2;
  background-color: rgb(7 30 118);
  border-radius: 15px;
  -webkit-transform: translateY(40%);
          transform: translateY(40%);
  transition: 0.25s ease-in-out;
}

.card__header {
  position: relative;
  display: flex;
  align-items: center;
  grid-gap: 1.5em;
  gap: 1.5em;
  padding: 1.125em;
  border-radius: 40px 0 0 0;
}
.card__header p {
  font-size: 0.8em;
  color: white;
  font-weight: 100;
}
.card_overlay:hover{
  -webkit-transform: translateY(14%);
          transform: translateY(14%);
}

.uploader_image {
  border-radius: 50%;
  border: 4px solid #ffffff8c;
}

.btn {
  font-size: 1.25rem;
  border: 1px solid black;
  border-radius: 9px;
  -webkit-transform: scale(1);
          transform: scale(1);
  background-color: #000000;
  padding: 5px;
  transition: 0.25s ease-in-out;
  color: white;
  cursor: pointer;
  transition: 0.25s ease-in-out;
}
.btn:hover {
  -webkit-transform: scale(1.08);
          transform: scale(1.08);
  /* box-shadow: -6px -6px 10px rgba(255, 255, 255, 0.8),
    6px 6px 10px rgba(0, 0, 0, 0.2); */
}

#header-search {
  font-size: 1.02rem;
  border: 1px solid black;
  height: 2em;
  width: 17em;
  border-radius: 5px;
}


.pages{
  display: flex;
  padding: 20px;
}
.pages div{
  padding: 5px;
  margin: auto;
}

.footer footer
{
  background-color: #000000;
  color: white;
  height: 60px;
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1rem;
}
.mid{
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.4);
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 1;
    -webkit-transform: scale(1) ;
            transform: scale(1) ;
    transition: 0.4s ease-in-out;
}
.mid:hover, .mid:active ,.mid:focus{
    opacity: 1;
}
@-webkit-keyframes zoom {
    from {-webkit-transform:scale(0);transform:scale(0)} 
    to {-webkit-transform:scale(1);transform:scale(1)}
}
@keyframes zoom {
    from {-webkit-transform:scale(0);transform:scale(0)} 
    to {-webkit-transform:scale(1);transform:scale(1)}
}
.glass{
    width: 97%;
    height: 97%;
    display: flex;
    flex-direction: row;
    align-content: stretch;
    object-fit: cover;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.31);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(13.5px);
    -webkit-backdrop-filter: blur(13.5px);
    border: 1px solid rgba(0, 0, 0, 0.6);
}
.imagepreview{
    border-radius: 15px;
    padding: 5px;
    width: auto;
    height: auto;
    max-width: 98%;
    max-height: 98%;
    object-fit: contain;
}


